<template>
  <div>
    <Appbar :title="barTitle" :icon="barIcon" />
    <v-content>
      <v-container fluid>
        <!-- <v-snackbar v-model="snackbar">
          已成功复制到你的剪贴板
          <v-btn color="red" text @click="snackbar = false">关闭</v-btn>
        </v-snackbar>-->
        <v-alert text dense color="teal" icon="mdi-information-outline" border="left">
          你也可以将你的需求直接发送到QQ
          <strong>1224657851</strong>
        </v-alert>
        <v-form ref="form" v-model="valid" lazy-validation>
          <v-text-field v-model="name" :counter="4" :rules="nameRules" label="姓名" required></v-text-field>
          <v-text-field
            v-model="designNmae"
            :counter="20"
            :rules="designNameRules"
            label="设计名称"
            required
          ></v-text-field>
          <v-text-field
            v-model="email"
            :counter="11"
            :rules="contactRules"
            label="联系方式/QQ/电话/微信"
            required
          ></v-text-field>
          <v-textarea filled label="简述需求" hint="可以将开题粘贴到此"></v-textarea>
          <!-- <v-checkbox
            v-model="checkbox"
            :rules="[v => !!v || 'You must agree to continue!']"
            label="Do you agree?"
            required
          ></v-checkbox>-->

          <v-btn :disabled="!valid" color="success" class="mr-4" @click="validate">验证</v-btn>

          <v-btn color="error" class="mr-4" @click="reset">清空</v-btn>

         
        </v-form>
      </v-container>
    </v-content>
  </div>
</template>
<script>
import Appbar from "@/components/appbar.vue";
export default {
  components: {
    Appbar
  },
  data() {
    return {
      snackbar: false,
      barTitle: "深度定制",
      barIcon: "mdi-arrow-left",
      valid: true,
      name: "",
      designNmae: "",
      nameRules: [
        v => !!v || "请填写名称",
        v => (v && v.length <= 4) || "名称不能超过4位"
      ],
      designNameRules: [
        v => !!v || "请填写设计名称",
        v => (v && v.length <= 4) || "设计名称不能超过4位"
      ],
      email: "",
      contactRules: [
        v => !!v || "请填写联系方式",
        v => (v && v.length <= 11) || "不能超过11位"
      ],
      select: null,
      items: ["Item 1", "Item 2", "Item 3", "Item 4"],
      checkbox: false
    };
  },

  methods: {
    validate() {
      if (this.$refs.form.validate()) {
        var captcha1 = new TencentCaptcha("2058252892", res => {
          console.info(captcha1);
        });
        captcha1.show();
      }
    },
    reset() {
      this.$refs.form.reset();
    },
    resetValidation() {
      this.$refs.form.resetValidation();
    }
  }
};
</script>